.wrapper{
    display:flex;
    flex-wrap:wrap;
    justify-content:center;
    align-items:center; }
.grid { 
    display:grid; 
    grid-auto-flow:column;
}
.img { 
    max-width:100%; 
    max-height:100%;
    object-fit: cover;
    object-position:center; }
.fit {
    height:100%;
    width:100%; }

.fit-h { 
    height:100%;    }

.fit-w { 
    width:100%; }

.pill {
    border-radius:35% 50% 20% 20%/ 35% 50% 20% 20% ;
}